.form-border{
    padding: 50px 0 80px;
    position: relative;
    &::before{
        content: '';
        width: 100%;
        height: calc(95px + 50px);
        background-color: #F1F5F6;
        position: absolute;
        top: 0;
        left: 0;
    }
    .border{
        display: flex;
        justify-content: center;
        background-image: url(../images/form-border.svg);
        background-size: cover;
        padding: 5px;
        width: 100%;
        position: relative;
        z-index: 10;
    }
    &__text{
        font-size: 28px;
        line-height: 130%;
        color: #000000;
        height: 180px;
        width: 430px;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        background-color: #fff;
        &::before{
            content: '';
            position: absolute;
            right: 0;
            top: 0;
            height:calc(50% + 3px);
            width: 1px;
            transform: rotate(-20deg);
            background-color: #C4C4C4;
        }
        &::after{
            content: '';
            position: absolute;
            right: 0;
            bottom: 0;
            height: calc(50% + 3px);
            width: 1px;
            transform: rotate(20deg);
            background-color: #C4C4C4;
        }
        b, strong{
            font-size: 32px;
            line-height: 130%;
            display: block;
        }
    }
    &__form{
        width: calc(100% - 430px);
        padding: 10px 55px 10px 80px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #fff;
        input[type='text']{
            padding: 10px 15px;
            border: 0;
            border-bottom: 1px solid  #ACACAC;
            max-width: 300px;
            width: 100%;
            transition: .3s;
            margin-right: 40px;
            &:focus{
                border-color: #767676;
            }

        }
        input[type='email']{
            padding: 10px 15px;
            border: 0;
            border-bottom: 1px solid  #ACACAC;
            max-width: 300px;
            width: 100%;
            transition: .3s;
            margin-right: 40px;
            &:focus{
                border-color: #767676;
            }

        }
        input::placeholder{
            font-size: 16px;
            line-height: 120%;
            color: #000000;
            opacity: .8;
            font-family: "montserat";
        }
        input[type='submit']{
            padding: 12px 30px;
            box-shadow: none;
            border: 2px solid #EF4949;
            border-radius: 5px;
            color: #EF4949;
            background-color: transparent;
            transition: .3s;
            cursor: pointer;
            font-family: "montserat";
            &:hover{
                background-color: #EF4949;
                color: #fff;
            }
        }
    }
}